@media screen and (max-width: 1090px){
  .cards {
    gap: 3.058103975535168vh; // 30px in viewport heigth 980px
    grid-template: 
    "supervisor team" 1fr
    "karma calculator" 1fr
    ;
  }
  .cards > div{
    max-width: 350px; 
}
}
@media screen and (max-width: 800px){
  .header {
    h1, h2 {
      font-size: 1.5rem;
    }
    p {
      max-width: 350px;
      margin-bottom: 30px;
    }
  }
  .footer {
    max-width: 350px;
    text-align: center;
  }
  .cards {
    justify-content: center;
    // gap: 30px;
    gap: 3.058103975535168vh; // 30px in viewport heigth 980px
    grid-template: 
    "supervisor" 1fr
    "team"       1fr
    "karma"      1fr
    "calculator" 1fr
    ;
  }
  .cards > div{
    max-width: 350px; 
    // height: 250px; 
}
}



@media screen and (max-width: 450px){
  .header {
