body {
  background: $light-gray;
  font-size: $font-size;
  font-family: $font-family;
}

.container {
  max-width: 1440px;
  // width: 100vw;
  // height: 100vh;
  margin: 60px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

// Header

// 2.038735983690112vh 20px in viewport 981px heigth 
// 3.058103975535168vh 30px in viewport 981px heigth 
// 6.116207951070336vh 60px in viewport 981px heigth 

.header {
  text-align: center;
  line-height: 1.5em;
  h1 {
    margin-bottom: 2.038735983690112vh;
    font-size: 2.25em;
    font-weight: 200;
  }
  h2 {
    margin-bottom: 3.058103975535168vh;
    font-size: 2.25em;
    font-weight: 600;
  }
  p {
    margin: 0 auto 6.116207951070336vh auto; //left and right auto 60px in viewport 920px
    max-width: 500px;
    font-size: 1em; 
    color: $grayish-blue;
    line-height: 1.5em;
  }
}

// Cards

.cards {
  max-width: 1440px;
  line-height: 1.5em;
  display: grid;
  gap: 30px;
  grid-template: 
  "    .      team          . " 1fr
  "supervisor team  calculator" 1fr
  "supervisor karma calculator" 1fr
  ".          karma          ." 1fr

  ;
}

.cards > div{
  max-width: 350px;
  padding: 30px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.05), 0 6px 6px rgba(0, 0, 0, 0.1);
  border-top: 5px solid;
  border-radius: 5px;
  // box-shadow: 1px 6px 16px $grayish-blue;
  // box-sizing: border-box;
  display: flex;
  flex-direction: column;
  // min-width: 24.306vw; //350px in viewport 1440px
  // height: 27.173913043478258vh; //250px in viewport 920px
 
  // height: 250px;
  span {
    font-size: 1.25em;
    font-weight: 600;
    margin-bottom: 20px;
  }
  p{
    color: $grayish-blue;
    margin-bottom: 40px;
  }
  img {
    display: block;
    margin: 0px;
    padding: 0px;
    align-self: flex-end;
    justify-self: flex-end;
  }
}

.cards div:nth-child(1){
  grid-area: supervisor;
  border-color: $cyan;
  }
.cards div:nth-child(2){
  grid-area: team;
  border-color: $red;
}
.cards div:nth-child(3){
  grid-area: karma;
  border-color: $orange;
}
.cards div:nth-child(4){
  grid-area: calculator;
  border-color: $blue;
}

// footer
.footer {
  margin-top: 8.695652173913043vh; // 80px in viewport 920px
  line-height: 1.5em;
}